<template>
  <div class="locataire1">
    <div style="z-index:1" class="lPBFCTitle">
        <div class="lPBFCCross" @click="$router.go(-1);"><img src="@/assets/Retour.png"/></div>
        {{ locataire.nom }}
    </div>
    <div class="locat1Body">
      <div class="locat1Info locat1Block">
        <p class="locat1Head">Informations</p>
        <div class="locat1InfoImg"><img :src="$store.state.baseUrl+locataire.image"/></div>
        <div><div>Nom </div><span>{{ locataire.nom }}</span></div>
        <div><div>Prenom</div><span>{{ locataire.prenom }}</span></div>
        <div><div>Tel 1</div><span>{{ locataire.tel1 }}</span></div>
        <div><div>Tel 2</div><span>{{ locataire.tel2 }}</span></div>
        <div><div>Age</div><span>{{ locataire.age }} ans</span></div>
        <div><div>Situation</div><span>{{ locataire.situation_matrimonial }}</span></div>
      </div>
      <div v-for="(ctr, index) in contrats" :key="index" class="locat1Contrat locat1Block">
        <p class="locat1Head">Chambre {{ ctr.chambreNom }}</p>
        <div><div>Cité</div><span>{{ ctr.citeNom }}</span></div>
        <div><div>Date début</div><span>{{ dateFormat(ctr.date_debut) }}</span></div>
        <div><div>Prix/mois</div><span>{{ ctr.loyer }} FCFA</span></div>
        <div><div>Dette | avance</div><span style="text-decoration:underline" :style="{color:balanceColor(ctr.balance)}">{{ setBalance(ctr) }}FCFA</span></div>
        <div><div>Nombre mois payé</div><span>{{ ctr.nbrMoisPayer }} </span></div>
        <div style="font-weight:bold"><div>date d'expiration</div><span>{{ expiration(ctr) }} </span></div>
        <div><div>Total payé </div><span>{{ ctr.totalPayer }} FCFA</span></div>
        <div><div>Total Charge+Décharge </div><span>{{ ctr.totalCharge }} FCFA</span></div>
        <img v-show="$store.state.login.isAdmin == 1" @click="showMenu(ctr)" class="locat1Menu" src="./img/menu.svg"/>
      </div>
    </div>
    <EditMenu :elements='menu'  @select="select" ref="menu" />
    <YesNo ref="yesNo" @option='yesNoAction'/>
    <ModifyLocataire ref='modifier' @modified='setContrat' style="z-index:1" />
    <YAlert ref="yAlertS" type="sucess" />
    <LLoading1 :visible="loading"/>
  </div>
</template>
<script src="./locataire.js">
</script>
<style src="./locataire.css">
</style>